import bus from '@/bus'

export default({
    state:{
        disCoupon:[]
    },
    mutations:{
        SET_DISCOUN(state,obj){
            state.disCoupon = obj;
            console.log(this.state.disCoupon)
        }
    },
    actions:{
        getCoupon({commit}){
            bus.$axios.post("pawn/discount/list")
            .then(({data})=>{
                this.state.Province = data.data;
                console.log(data.data)
                commit("SET_DISCOUN",data.data);

                // commit("SET_DISCOUN",{
                //     disCoupon:data.data
                // })
            })
        }
    }
})