import bus from '@/bus'

export default({
    state:{
        time: '',
        oderNum: '',
        inflow: '',
        balance:'',
        moneyTo:''
    },
    mutations:{
        SET_MUTATION_TABLE(state,obj){
            state.time = obj[0].time;
            console.log(state.time)
            state.oderNum = obj[0].orderNumber;
            state.balance = obj[0].balance;
            state.moneyTo = "转入银行卡"
        },
        SET_MUTATION_EMPTY(state,params){
            state.balance = params
            //console.log("bbbb")
        }
    },
    actions:{
        getActionTable({commit},obj){
            bus.$axios.post('/user/user/assetDetail')
            .then(({data})=>{
                //console.log(data)
                commit("SET_MUTATION_TABLE",data.assetDetailList)
            })
        },
        handleActionsEmpty({commit},params){
            commit("SET_MUTATION_EMPTY",params)
        }
    }
})