import bus from '@/bus'

export default({
    state:{
        goodsAccMoney:[]
    },
    mutations:{
        SET_MUTATIONACC(state,obj){
            state.goodsAccMoney = obj
            console.log(state.goodsAccMoney)
        },
        HANDLEMUTATIONSEMPTY(state){
            state.goodsAccMoney.usableMoney = "0.00"
        }
    },
    actions:{
        getActionAccount({commit}){
            bus.$axios.post('/user/user/account')
            .then(({data})=>{
                commit("SET_MUTATIONACC",data.account)
            })
        },
        handleActionsEmpty({commit}){
            commit("HANDLEMUTATIONSEMPTY")
        }
    }
})