import bus from '@/bus'

export default({
    state:{
        truemodel:{},
        evamodel:{},
        evamodelin:[]
    },
    mutations: {
        SET_GOODS_MODEL(state, obj) {
            state.truemodel = obj
        },
        SET_TRUE_MODEL(state, obj) {
            state.evamodel = obj
        },
        SET_TRUE_MODELCONTENT(state,obj){
            state.evamodelin = obj
        }
    },
    actions:{
        getTitle({commit},obj){
            bus.$axios.post("/pawn/products/findById/"+obj.proId)
                .then(({data})=>{
                    // this.state.ConfigurationSelection.list = data.data
                    commit("SET_GOODS_MODEL",data.data);
                })
        },
        setTitle({commit},obj){
            bus.$axios.post("/pawn/evaluate",{
                proId:obj.proId,
                brandId:obj.brandId,
                model:obj.model,
                peakPrice:obj.peakPrice,
                productsAddress:obj.productsAddress,
                pageView:obj.pageView,
                creationTime:obj.creationTime,
                releaseTime:obj.releaseTime,
                percent:obj.percent,
            }).then(({data})=>{
                console.log(data);
                commit("SET_TRUE_MODEL",data);
                commit("SET_TRUE_MODELCONTENT",data.data);
            })
        },

        // handleRouterThird({commit},obj){
        //     bus.$axios.get("/pawn/products/findById/1?typeId="+obj.typeId+"&brandId="+obj.brandId+"&offset="+obj.offset+"&limit="+obj.limit)
        //     .then(({data})=>{
        //        console.log(data,1111111111)
        //          commit("SET_MIDDLE",data.rows);
        //     })
        // }
    }
})