import bus from "@/bus"
export default{
    state:{
        typeName:[]
    },
    mutations:{
        SET_TYPE_TYPENAME(state,obj){
            state.typeName = obj
            console.log(state.typeName)
        }
    },
    actions:{
        getTitle({commit}){
            // console.log(123);
            bus.$axios.post("/pawn/type/list")
            .then(({data})=>{
                commit("SET_TYPE_TYPENAME",data.data)
                // console.log(this.state.typeName);
            })
        }
    }
}